/* ==========================================================================
  c-spinner
 ========================================================================== */

.c-spinner {
  display: inline-block;
  width: 4rem;
  height: 4rem;
  vertical-align: text-bottom;
  border: .4rem solid $color-black;
  border-right-color: transparent;
  border-radius: 50%;
  animation: spinner 1.3s linear infinite;
}

@keyframes spinner {
  to {transform: rotate(360deg);}
}
